<template>
  <Toast />
  <!-- create -->
  <Dialog
    v-model:visible="createProductModalVisible"
    modal
    header="Create product"
    :style="{ width: '25rem' }"
  >
    <div class="flex items-center gap-4 mb-4">
      <label for="name" class="font-semibold w-24">Name</label>
      <InputText
        v-model:modelValue="selectedProduct!.name"
        id="name"
        class="flex-auto"
        autocomplete="off"
      />
    </div>
    <div class="flex items-center gap-4 mb-4">
      <label for="description" class="font-semibold w-24">description</label>
      <InputText
        v-model:modelValue="selectedProduct!.description"
        id="description"
        class="flex-auto"
        autocomplete="off"
      />
    </div>
    <div class="flex items-center gap-4 mb-4">
      <label for="quantity" class="font-semibold w-24">quantity</label>
      <InputText
        v-model:modelValue="selectedProduct!.quantity"
        type="number"
        id="quantity"
        class="flex-auto"
        autocomplete="off"
      />
    </div>
    <div class="flex items-center gap-4 mb-4">
      <label for="price" class="font-semibold w-24">price</label>
      <InputText
        v-model:modelValue="selectedProduct!.price"
        type="number"
        id="price"
        class="flex-auto"
        autocomplete="off"
      />
    </div>

    <div class="flex justify-center gap-4">
      <Button
        class="w-1/2"
        severity="secondary"
        label="Cancel"
        @click.prevent="() => (createProductModalVisible = false)"
      />
      <Button
        @click.prevent="handleCreateConfirm"
        class="w-1/2"
        label="Create"
      />
    </div>
  </Dialog>
  <!-- edit -->
  <Dialog
    v-model:visible="editProductModalVisible"
    modal
    header="Edit product"
    :style="{ width: '25rem' }"
  >
    <div class="flex items-center gap-4 mb-4">
      <label for="name" class="font-semibold w-24">Name</label>
      <InputText
        v-model:model-value="selectedProduct!.name"
        id="name"
        class="flex-auto"
        autocomplete="off"
      />
    </div>
    <div class="flex items-center gap-4 mb-4">
      <label for="description" class="font-semibold w-24">description</label>
      <InputText
        v-model:model-value="selectedProduct!.description"
        id="description"
        class="flex-auto"
        autocomplete="off"
      />
    </div>
    <div class="flex items-center gap-4 mb-4">
      <label for="quantity" class="font-semibold w-24">quantity</label>
      <InputText
        v-model:model-value="selectedProduct!.quantity"
        type="number"
        id="quantity"
        class="flex-auto"
        autocomplete="off"
      />
    </div>
    <div class="flex items-center gap-4 mb-4">
      <label for="price" class="font-semibold w-24">price</label>
      <InputText
        v-model:model-value="selectedProduct!.price"
        type="number"
        id="price"
        class="flex-auto"
        autocomplete="off"
      />
    </div>

    <div class="flex justify-center gap-4">
      <Button
        class="w-1/2"
        severity="secondary"
        label="Cancel"
        @click="() => (createProductModalVisible = false)"
      />
      <Button
        severity="warn"
        @click="handleConfirmEditProduct"
        class="w-1/2"
        label="Edit"
      />
    </div>
  </Dialog>
  <!-- delete -->
  <Dialog
    v-model:visible="deleteProductModalVisible"
    modal
    header="Delete product"
    :style="{ width: '25rem' }"
  >
    <div class="flex flex-col justify-center gap-4">
      <h2>Are you sure you want to delete {{ selectedProduct?.name }}?</h2>
      <div class="flex flex-row gap-2">
        <Button
          class="w-1/2"
          severity="secondary"
          label="Cancel"
          @click="() => (deleteProductModalVisible = false)"
        />
        <Button
          @click="handleConfirmDeleteProduct"
          severity="danger"
          class="w-1/2"
          label="Delete"
        />
      </div>
    </div>
  </Dialog>

  <header class="bg-teal-400">
    <nav class="flex justify-between w-full bg-teal-500 text-white p-4">
      <a href="#"
        ><span class="font-semibold text-2xl tracking-tight"
          >Mis productos</span
        ></a
      >
      <div class="md:items-center md:w-auto flex">
        <div class="flex text-sm gap-2">
          <Button severity="secondary" @click.prevent="handelCreateProduct">
            <span class="text-teal-500">Agregar</span>
          </Button>
          <Button label="logout" severity="warn" @click.prevent="handleLogout">
          </Button>
        </div>
      </div>
    </nav>
  </header>
  <main class="p-2">
    <div class="flex flex-row items-start w-full gap-4 mb-4">
      <Card class="w-1/2">
        <template #content>
          <Skeleton
            width="10rem"
            height="1rem"
            v-if="productsLength === null"
          ></Skeleton>
          <p v-else class="text-5xl font-bold text-teal-500 text-center">
            {{ productsLength }}
          </p>
        </template>
        <template #footer>
          <Divider />
          <p>Producto(s) registrado(s)</p>
        </template>
      </Card>
      <Card class="w-1/2">
        <template #content>
          <Skeleton
            width="10rem"
            height="1rem"
            v-if="fullQuantity === null"
          ></Skeleton>
          <p v-else class="text-5xl font-bold text-teal-500 text-center">
            {{ fullQuantity }}
          </p>
        </template>
        <template #footer>
          <Divider />
          <p>Producto(s) restante(s)</p>
        </template>
      </Card>
      <div></div>
    </div>
    <div
      v-if="products === null"
      class="w-full flex flex-row items-center justify-center"
    >
      <Spinner />
    </div>
    <h2 v-else-if="products.length === 0">Sin productos</h2>
    <Card v-else>
      <template #content>
        <DataTable :value="products" tableStyle="min-width: 50rem">
          <!-- <Column field="image" header="image"></Column> -->
          <Column field="name" header="Name"></Column>
          <Column field="description" header="Description"></Column>
          <Column field="quantity" header="Quantity"></Column>
          <Column field="price" header="Price"></Column>
          <Column
            header="Actions"
            class="flex flex-row items-center gap-2 justify-start"
          >
            <template #body="slotProps">
              <Button
                @click="handleEditProduct(slotProps.data)"
                severity="secondary"
                icon="pi pi-pencil"
                class="w-1/4 p-mr-2"
                label="Edit"
              ></Button>
              <Button
                severity="danger"
                icon="pi pi-trash"
                class="w-1/4"
                label="Delete"
                @click="handleDeleteProduct(slotProps.data)"
              ></Button>
            </template>
          </Column>
        </DataTable>
      </template>
    </Card>
  </main>
</template>

<script setup lang="ts">
import { computed, onMounted, ref } from "vue";

import Dialog from "primevue/dialog";
import Toast from "primevue/toast";
import DataTable from "primevue/datatable";
import Column from "primevue/column";
import Card from "primevue/card";
import Divider from "primevue/divider";
import Spinner from "primevue/progressspinner";
import InputText from "primevue/inputtext";

import Product from "@/@types/Product";

import Button from "primevue/button";
import Skeleton from "primevue/skeleton";
import {
  getProducts,
  createProduct,
  deleteProduct,
  updateProduct,
} from "@/services/products";

import senNotification from "@/services/push-not";

const fullQuantity = ref<number | null>(null);
const productsLength = computed(() => products.value?.length);
const products = ref<Product[] | null>(null);

const createProductModalVisible = ref(false);
const deleteProductModalVisible = ref(false);
const editProductModalVisible = ref(false);

const selectedProduct = ref<Product | null>(null);

const handleLogout = () => {
  localStorage.removeItem("user");
  window.location.href = "/";
};

const handleEditProduct = (product: Product) => {
  selectedProduct.value = { ...product };
  editProductModalVisible.value = true;
};
const handleConfirmEditProduct = async () => {
  if (!selectedProduct.value) return;
  editProductModalVisible.value = false;
  await updateProduct(selectedProduct.value.id, selectedProduct.value);
  senNotification(
    `Product updated`,
    `Product ${selectedProduct.value.name} has been updated`
  );
  window.location.reload();
};
const handleDeleteProduct = (product: Product) => {
  selectedProduct.value = product;
  deleteProductModalVisible.value = true;
};
const handleConfirmDeleteProduct = async () => {
  if (!selectedProduct.value) return;
  await deleteProduct(selectedProduct.value.id);
  senNotification(
    `Product deleted`,
    `Product ${selectedProduct.value.name} has been deleted`
  );
  window.location.reload();
};

const handelCreateProduct = () => {
  selectedProduct.value = {
    id: -1,
    name: "",
    description: "",
    quantity: "",
    price: "",
    image: "",
  };
  createProductModalVisible.value = true;
};
const handleCreateConfirm = async () => {
  if (!selectedProduct.value) return;
  createProductModalVisible.value = false;
  await createProduct(selectedProduct.value);
  senNotification(
    `Product created`,
    `Product ${selectedProduct.value.name} has been created`
  );
  window.location.reload();
};

onMounted(async () => {
  const data = await getProducts();
  fullQuantity.value = data.reduce(
    (acc, product) => acc + Number(product.quantity),
    0
  );
  products.value = data;
});
</script>
